
:root
    // Цветовая палитра
    --color-gray: #ABB2BF
    --color-background: #282C33
    --color-primary: #27DEBF
    --color-accent: #fff
    --color-black: #111111
    --color-gray-dark: #808080
    --color-selection: #27dec075

    // Типографика
    --font-family: 'Fira Code', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif
    --font-size-base: 1rem
    --font-size-md: 1.1rem
    --font-size-lg: 1.2rem
    --font-size-xl: 1.4rem
    --font-size-xxl: 1.6rem
    --font-size-heading: 1.8rem
    --font-size-icon-base: 1.3rem
    --font-size-icon: 1.8rem
    --font-size-symbol: 2rem
    --line-height-sm: 1.2rem
    --line-height-base: 1.6rem
    --line-height-lg: 1.8rem

    // Размеры и отступы
    --spacing-xs: 5px
    --spacing-sm: 8px
    --spacing-mds: 10px
    --spacing-md: 16px
    --spacing-lg: 20px
    --spacing-xl: 24px
    --spacing-xxl: 30px
    --spacing-section: 50px
    --spacing-large-section: 100px
    --spacing-body: 200px
    --border-radius-sm: 6px
    --border-radius-md: 8px
    --border-radius-lg: 15%
    --border-radius-full: 50%

    // Анимации
    --spring-duration: 4s
    --spring-height: -40px
    --squash-factor: 1.1
    --transition-base: 0.3s
    --transition-slow: 1s
    --transition-michelangelo: 0.8s cubic-bezier(0.68, -0.55, 0.27, 1.55)
    --transition-pizzabox: 1.2s cubic-bezier(0.68, -0.55, 0.27, 1.55)

    // Тени
    --shadow-primary: 0 0 2px rgba(39, 222, 191, 0.3)

    // Размеры элементов
    --cursor-size: 10px
    --cursor-hover-size: 15px
    --border-width: 1px
    --border-width-thick: 2px
