
.customCursor
    position: fixed
    width: var(--cursor-size)
    height: var(--cursor-size)
    background-color: var(--color-accent)
    border-radius: 50%
    pointer-events: none
    z-index: 1000
    transition: transform var(--transition-base) ease-out

.emphasized
    width: 4px
    height: 30px
    border-radius: 15%
    background-color: var(--color-primary)

.hovered
    width: var(--cursor-hover-size)
    height: var(--cursor-hover-size)
    border-radius: 50%
    background-color: white
