
.contactsContainer
    padding: var(--spacing-large-section) var(--spacing-body) var(--spacing-body) var(--spacing-body)
    text-align: start
    position: relative
    .contactsDescription
        margin-top: var(--spacing-xl)
        color: var(--color-gray)
        font-size: var(--font-size-md)
        line-height: var(--line-height-base)
    .contactsContentSection
        margin-top: var(--spacing-section)
        display: flex
        justify-content: space-between
        .contactsText
            width: 500px
            color: var(--color-gray)
            font-size: var(--font-size-md)
            line-height: var(--line-height-base)
        .contactsSocial
            padding: var(--spacing-md)
            border: var(--border-width)solid var(--color-gray)
            width: fit-content
            display: flex
            flex-direction: column
            gap: var(--spacing-md)
            margin: 0 10% 0 30%
            position: relative
            .socialHeader
                color: var(--color-accent)
                font-size: var(--font-size-lg)
            .socialContainer
                display: flex
                transition: var(--transition-base)
                .socialIcon
                    color: var(--color-gray)
                    font-size: var(--font-size-heading)
                    transition: var(--transition-base)
                .socialUrl
                    text-decoration: none
                    font-size: var(--font-size-lg)
                    margin: auto 0
                    margin-left: var(--spacing-sm)
                    color: var(--color-gray)
                    transition: var(--transition-base)
            .socialContainer:hover
                .socialIcon, .socialUrl
                    color: var(--color-primary)
            .squareLines1
                position: absolute
                width: 44px
                bottom: -15%
                right: -70%
            .linesFigure1
                position: absolute
                top: -30%
                left: -130%
                animation: springBounce var(--spring-duration) infinite ease-in-out

    .squareLines1
        position: absolute
        right: -3%
        top: 10%
        width: 128px
        animation: springBounce var(--spring-duration) infinite ease-in-out
    .squareLines2
        position: absolute
        right: -3%
        top: 70%
        width: 112px
        animation: springBounce var(--spring-duration) infinite ease-in-out
    .squareDots1
        position: absolute
        left: -1%
        top: 90%
        animation: springBounce var(--spring-duration) infinite ease-in-out
    .squareDots2
        position: absolute
        right: 3%
        top: 40%
        width: 96px
        animation: springBounce var(--spring-duration) infinite ease-in-out

@keyframes springBounce
    0%, 100%
        transform: translateY(0) scale(1)
        animation-timing-function: ease-out
    20%
        transform: translateY(var(--spring-height)) scale(1.1, 0.9)
        animation-timing-function: ease-in
    40%
        transform: translateY(0) scale(0.95, var(--squash-factor))
        animation-timing-function: ease-out
    60%
        transform: translateY(calc(var(--spring-height) * 0.7)) scale(1.05, 0.95)
        animation-timing-function: ease-in
